        font-weight: normal;
        src: local('Robotaur'), url(/robofriends/static/media/robotaur.c16ca397.woff) format('woff');
        }

        h1 {
            font-family: 'Robotaur';
            text-align: center;
            color: rgb(255, 255, 255);
            margin: 15px;
            padding: 15px;  
        }
